import commonCoreStrings from 'kolibri.coreVue.mixins.commonCoreStrings';

export default {
  name: 'OnboardingForm',
  mixins: [commonCoreStrings],
  props: {
    header: {
      type: String,
      required: true,
    },
    description: {
      type: String,
      required: false,
    },
    submitText: {
      type: String,
      required: false,
    },
  },
  computed: {
    hasDescription() {
      return this.description || this.$slots.description;
    },
  },
};

